import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { Box, Paper, Checkbox, Chip, Grid } from '@mui/material';

import { NewDataTable } from '@/components';

function EnhancedTable(props) {
    const { commonStates } = props;
    return (
        <Box sx={{ width: '100%' }}>
            <Paper sx={{ width: '100%', mb: 2, background: '' }}>
                <NewDataTable commonStates={commonStates} />
            </Paper>
        </Box>
    );
}

export default EnhancedTable;
