import React from 'react';
import ReactModal from 'react-modal-resizable-draggable';
import './index.scss';

function Modal({ isOpenModal, closeModal, children, title = 'Окно можно переносить', width = 800, height = 600 }) {
    return (
        <div className="modal">
            <ReactModal initWidth={width} initHeight={height} onRequestClose={closeModal} isOpen={isOpenModal}>
                <h3>{title}</h3>
                <br />
                {children}
            </ReactModal>
        </div>
    );
}

export default Modal;
