import React, { Fragment } from 'react';
import { nanoid } from 'nanoid';
import { Box, Divider } from '@mui/material';

function ButtonsBoxRow(props) {
    const { children } = props;
    const len = children && children.length ? children.length - 1 : 0;

    function getMultiChildrens() {
        return children.map((el, index) => (
            <Fragment key={nanoid(8)}>
                {el}
                {len !== index && <Divider orientation="vertical" variant="large" flexItem />}
            </Fragment>
        ));
    }

    return (
        <Box
            sx={{
                display: 'flex',
                alignItems: 'center',
                width: 'fit-content',
                border: (theme) => `1px solid ${theme.palette.divider}`,
                borderRadius: 1,
                bgcolor: 'background.paper',
                color: 'text.secondary',
                height: '100%',
                p: 1.5,
                '& hr': {
                    mx: 1.5
                }
            }}
        >
            {len === 0 ? children : getMultiChildrens()}
        </Box>
    );
}

export default ButtonsBoxRow;
