import * as constants from './constants';

const initialState = [];
const map = new Map();
const reducer = (state = initialState, action) => {
    map.set(constants.ADD_ALERT_TO_LIST, [...state, { show: true, ...action.payload }]).set(constants.REMOVE_FIRST_ALERT_FROM_LIST, [
        ...state.slice(1)
    ]);
    if (map.has(action.type)) {
        return map.get(action.type);
    }
    return state;
};

export default reducer;
