import React, { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { addAlertToList, removeFirstAlert } from '@/core/store/alerts/actions';
import './index.scss';

function Alert() {
    const dispatch = useDispatch();
    const { alerts } = useSelector((state) => ({
        alerts: state.alerts
    }));

    useEffect(() => {
        const timer = setTimeout(() => dispatch(removeFirstAlert()), 4000);
        return () => clearTimeout(timer);
    }, [alerts.length]);

    return (
        <div className="notification">
            {alerts &&
                alerts.length > 0 &&
                alerts.map((item, idx) => (
                    <div
                        className={`notification__item ${item.type}`}
                        style={{
                            display: item.show ? 'flex' : 'none',
                            left: '0%'
                        }}
                        key={idx}
                    >
                        <div className="notification__desc">{item.message}</div>
                    </div>
                ))}
        </div>
    );
}

function CallAlert({ type, message, children = null }) {
    const dispatch = useDispatch();

    useEffect(() => {
        dispatch(addAlertToList({ type, message }));
        return () => null;
    }, []);

    return null;
}

export { Alert, CallAlert };
