import * as constants from './constants';

const initialState = {
    pending: false,
    depositContract: null,
    signedDepositContract: null,
    bondAgreement: null,
    bondIssue: null,
    signedBondIssue: null
};

const map = new Map();
map.set(constants.CONSTRUCTOR_PENDING, 'pending')
    .set(constants.CREATE_DEPOSIT_CONTRACT, 'depositContract')
    .set(constants.CREATE_BOND_AGREEMENT, 'bondAgreement')
    .set(constants.CREATE_BOND_ISSUE, 'bondIssue')
    .set(constants.SIGN_DEPOSIT_CONTRACT, 'signedDepositContract')
    .set(constants.SIGN_BOND_ISSUE, 'signedBondIssue');

const reducer = (state = initialState, action) => {
    if (map.has(action.type)) {
        const key = map.get(action.type);
        return { ...state, pending: false, [key]: action.payload };
    }
    return state;
};

export default reducer;
