import React, { useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { Button, Box, InputLabel, MenuItem, FormControl, Select, TextField } from '@mui/material';
import { Modal } from '@/components';
import { addAlertToList } from '@/core/store/alerts/actions';
import { numberWithSpaces } from '@/core/utils/validate';
import SaveIcon from '@mui/icons-material/Save';
import CloseIcon from '@mui/icons-material/Close';
import CalculateIcon from '@mui/icons-material/Calculate';
import './handbookInterestRate.scss';

function ModalHandbookInterestRate({ commonStates, commonHandlers, title = 'Подписание договора' }) {
    const dispatch = useDispatch();
    const { columnsState, calcExpression, checkCalcExpression, uniqueVariableId, uniqueVariableName } = commonStates;
    let disabledSaveButton = false;
    function checkNum(str) {
        if (checkCalcExpression.length === 0) return false;
        try {
            const isTrue = window.eval(str);
            return true;
        } catch {
            return false;
        }
    }

    function hasContainVariable() {
        let test = false;
        columnsState
            .filter((item) => item.type === 'number')
            .forEach((el) => {
                if (calcExpression.indexOf(el.field) >= 0) {
                    test = true;
                }
            });
        return test;
    }

    function checkWord(word) {
        for (let i = 0; i < word.length; i++) {
            const ch = word.charAt(i).toLowerCase();
            if (ch < 'a' || ch > 'z') {
                if (ch !== '_') {
                    return true;
                }
            }
        }
        return false;
    }

    function hasContainUniqueId(word) {
        let test = false;
        columnsState
            .filter((item) => item.type === 'number')
            .forEach((el) => {
                if (word === el.field) {
                    test = true;
                }
            });
        return test;
    }
    function hasContainUniqueName(word) {
        let test = false;
        columnsState
            .filter((item) => item.type === 'number')
            .forEach((el) => {
                if (word === el.headerName) {
                    test = true;
                }
            });
        return test;
    }
    const displayBox = calcExpression.reduce((acc, value) => String(acc) + String(value), '') || '0';
    const displayBoxFalsy = checkCalcExpression.reduce((acc, value) => String(acc) + String(value), '');

    console.log(commonStates, '  ', commonHandlers);
    return (
        <Modal
            isOpenModal={commonStates.isOpenCreateModal}
            closeModal={commonHandlers.handleIsOpenCreateModal}
            title={title}
            width={800}
            height={640}
        >
            <Box gridColumn="span 12">
                <div style={{ margin: '10px 0px 10px 6px', display: 'flex', justifyContent: 'space-between' }}>
                    <TextField
                        id="uniqueVariableId"
                        label="Unique variable ID"
                        value={commonStates.uniqueVariableId}
                        onChange={commonHandlers.handleUniqueVariableId}
                    />
                    <TextField
                        id="uniqueVariableName"
                        label="Unique variable name"
                        value={commonStates.uniqueVariableName}
                        onChange={commonHandlers.handleUniqueVariableName}
                    />
                    <div style={{ display: 'flex', justifyContent: 'right' }}>
                        <Button
                            variant="contained"
                            startIcon={<SaveIcon />}
                            sx={{ maxHeight: '35px' }}
                            disabled={disabledSaveButton}
                            onClick={
                                () => {
                                    if (uniqueVariableId.length < 5 || uniqueVariableId.length > 35 || checkWord(uniqueVariableId)) {
                                        return dispatch(
                                            addAlertToList({
                                                type: 'error',
                                                message:
                                                    'The unique variable ID must contain only letters or the character _ and the length must be greater than 5 letters and less than 35'
                                            })
                                        );
                                    }
                                    if (hasContainUniqueId(uniqueVariableId)) {
                                        return dispatch(
                                            addAlertToList({
                                                type: 'error',
                                                message: 'Such a variable with such ID already exists'
                                            })
                                        );
                                    }
                                    if (uniqueVariableName.length < 5 || uniqueVariableName.length > 35 || checkWord(uniqueVariableName)) {
                                        return dispatch(
                                            addAlertToList({
                                                type: 'error',
                                                message:
                                                    'The unique variable name must contain only letters or the character _ and the length must be greater than 5 letters and less than 35'
                                            })
                                        );
                                    }
                                    if (hasContainUniqueName(uniqueVariableName)) {
                                        return dispatch(
                                            addAlertToList({
                                                type: 'error',
                                                message: 'Such a variable with such name already exists'
                                            })
                                        );
                                    }
                                    if (checkCalcExpression.length === 0) {
                                        return dispatch(addAlertToList({ type: 'error', message: 'Enter the formula' }));
                                    }
                                    try {
                                        const isTrue = window.eval(displayBoxFalsy);

                                        if (hasContainVariable() && !disabledSaveButton) {
                                            const counter = +localStorage.getItem('counter_f_i_e_l_d');
                                            localStorage.setItem(
                                                `f_i_e_l_d_${counter}*${uniqueVariableId}`,
                                                JSON.stringify({ headerName: uniqueVariableName, formul: calcExpression })
                                            );
                                            localStorage.setItem('counter_f_i_e_l_d', counter + 1);
                                            disabledSaveButton = true;
                                            window.location.reload();
                                            return commonHandlers.handleIsOpenCreateModal();
                                        }

                                        return dispatch(
                                            addAlertToList({
                                                type: 'error',
                                                message: 'Enter at least one variable or stop clicking'
                                            })
                                        );
                                    } catch {
                                        return dispatch(
                                            addAlertToList({
                                                type: 'error',
                                                message: 'The formula for the new variable is entered incorrectly'
                                            })
                                        );
                                    }
                                }
                                // dispatch(
                                //     saveContractSettingRate({
                                //         rate: issueRate,
                                //         rateSettingId,
                                //         daysInMonth,
                                //         authorId: 1
                                //     })
                                // )
                                //     .then(() => {
                                //         commonHandlers.handleReloadPage(true);
                                //         commonHandlers.handleSelectTableRow(null, null);
                                //     })
                                //     .finally(() => commonHandlers.handleIsOpenCreateModal())
                            }
                        >
                            Save
                        </Button>
                        <Button
                            variant="contained"
                            sx={{ maxHeight: '35px', marginLeft: '30px' }}
                            startIcon={<CloseIcon />}
                            onClick={commonHandlers.handleIsOpenCreateModal}
                        >
                            Close
                        </Button>
                    </div>
                </div>
                <form name="sci-calc" className="totalCalculator">
                    <table className="calculator" cellSpacing="1" cellPadding="1">
                        <thead className="theadOfButtons">
                            <tr>
                                <td colSpan="12">
                                    <textarea id="display" name="display" value={displayBox} contentEditable />
                                </td>
                            </tr>
                            <tr>
                                <td colSpan="12">
                                    <FormControl className="MuiTextField-root">
                                        <Select
                                            id="handbook-rate-value"
                                            label={null}
                                            value={commonStates.checkedColumn}
                                            onChange={commonHandlers.hanleCheckedColumn}
                                        >
                                            {columnsState
                                                .filter((item) => item.type === 'number')
                                                .map((item) => (
                                                    <MenuItem key={item.field} value={item.field}>
                                                        {item.headerName}
                                                    </MenuItem>
                                                ))}
                                        </Select>
                                    </FormControl>
                                    <Button
                                        variant="contained"
                                        startIcon={<CalculateIcon />}
                                        sx={{ maxHeight: '35px', marginLeft: '15px', marginTop: '5px' }}
                                        onClick={() =>
                                            commonHandlers.handleCalcExpression(
                                                [...calcExpression, commonStates.checkedColumn],
                                                [...checkCalcExpression, '(1)']
                                            )
                                        }
                                    >
                                        Insert variable
                                    </Button>
                                </td>
                            </tr>
                        </thead>
                        <tbody className="buttons">
                            <tr>
                                <td>
                                    <input
                                        type="button"
                                        className="btnTop"
                                        name="btnTop"
                                        value="C"
                                        onClick={() => commonHandlers.handleCalcExpression([], [])}
                                    />
                                </td>
                                <td>
                                    <input
                                        type="button"
                                        className="btnTop"
                                        name="btnTop"
                                        value="<--"
                                        onClick={() =>
                                            commonHandlers.handleCalcExpression(
                                                calcExpression.slice(0, -1),
                                                checkCalcExpression.slice(0, -1)
                                            )
                                        }
                                    />
                                </td>
                                <td>
                                    <input
                                        type="button"
                                        className="btnTop"
                                        name="btnTop"
                                        value="Check"
                                        onClick={() => {
                                            if (checkCalcExpression.length === 0) {
                                                return dispatch(addAlertToList({ type: 'error', message: 'Enter the formula' }));
                                            }
                                            try {
                                                const isTrue = window.eval(displayBoxFalsy);

                                                if (hasContainVariable()) {
                                                    return dispatch(
                                                        addAlertToList({
                                                            type: 'success',
                                                            message: 'The formula for the new variable is entered correctly'
                                                        })
                                                    );
                                                }

                                                return dispatch(
                                                    addAlertToList({
                                                        type: 'error',
                                                        message: 'Enter at least one variable'
                                                    })
                                                );
                                            } catch {
                                                return dispatch(
                                                    addAlertToList({
                                                        type: 'error',
                                                        message: 'The formula for the new variable is entered incorrectly'
                                                    })
                                                );
                                            }
                                        }}
                                    />
                                </td>
                                <td>
                                    <input
                                        type="button"
                                        className="btnOpps"
                                        name="btnOpps"
                                        value="&#960;"
                                        onClick={() =>
                                            commonHandlers.handleCalcExpression(
                                                [...calcExpression, '3.14159265359'],
                                                [...checkCalcExpression, '3.14159265359']
                                            )
                                        }
                                    />
                                </td>
                                <td>
                                    <input
                                        type="button"
                                        className="btnMath"
                                        name="btnMath"
                                        value="%"
                                        onClick={() =>
                                            checkNum(`(${displayBoxFalsy})/100`) &&
                                            commonHandlers.handleCalcExpression(
                                                ['(', ...calcExpression, ')/100'],
                                                ['(', ...checkCalcExpression, ')/100']
                                            )
                                        }
                                    />
                                </td>
                            </tr>
                            <tr>
                                <td>
                                    <input
                                        type="button"
                                        className="btnNum"
                                        name="btnNum"
                                        value="7"
                                        onClick={() =>
                                            commonHandlers.handleCalcExpression([...calcExpression, '7'], [...checkCalcExpression, '7'])
                                        }
                                    />
                                </td>
                                <td>
                                    <input
                                        type="button"
                                        className="btnNum"
                                        name="btnNum"
                                        value="8"
                                        onClick={() =>
                                            commonHandlers.handleCalcExpression([...calcExpression, '8'], [...checkCalcExpression, '8'])
                                        }
                                    />
                                </td>
                                <td>
                                    <input
                                        type="button"
                                        className="btnNum"
                                        name="btnNum"
                                        value="9"
                                        onClick={() =>
                                            commonHandlers.handleCalcExpression([...calcExpression, '9'], [...checkCalcExpression, '9'])
                                        }
                                    />
                                </td>
                                <td>
                                    <input
                                        type="button"
                                        className="btnOpps"
                                        name="btnOpps"
                                        value="x&#94;"
                                        onClick={() =>
                                            checkNum(`Math.exp(${displayBoxFalsy})`) &&
                                            commonHandlers.handleCalcExpression(
                                                ['exp(', ...calcExpression, ')'],
                                                ['Math.exp(', ...checkCalcExpression, ')']
                                            )
                                        }
                                    />
                                </td>
                                <td>
                                    <input
                                        type="button"
                                        className="btnMath"
                                        name="btnMath"
                                        value="/"
                                        onClick={() =>
                                            commonHandlers.handleCalcExpression([...calcExpression, '/'], [...checkCalcExpression, '/'])
                                        }
                                    />
                                </td>
                            </tr>
                            <tr>
                                <td>
                                    <input
                                        type="button"
                                        className="btnNum"
                                        name="btnNum"
                                        value="4"
                                        onClick={() =>
                                            commonHandlers.handleCalcExpression([...calcExpression, '4'], [...checkCalcExpression, '4'])
                                        }
                                    />
                                </td>
                                <td>
                                    <input
                                        type="button"
                                        className="btnNum"
                                        name="btnNum"
                                        value="5"
                                        onClick={() =>
                                            commonHandlers.handleCalcExpression([...calcExpression, '5'], [...checkCalcExpression, '5'])
                                        }
                                    />
                                </td>
                                <td>
                                    <input
                                        type="button"
                                        className="btnNum"
                                        name="btnNum"
                                        value="6"
                                        onClick={() =>
                                            commonHandlers.handleCalcExpression([...calcExpression, '6'], [...checkCalcExpression, '6'])
                                        }
                                    />
                                </td>
                                <td>
                                    <input
                                        type="button"
                                        className="btnOpps"
                                        name="btnOpps"
                                        value="ln"
                                        onClick={() =>
                                            checkNum(`Math.log(${displayBoxFalsy})`) &&
                                            commonHandlers.handleCalcExpression(
                                                ['log(', ...calcExpression, ')'],
                                                ['Math.log(', ...checkCalcExpression, ')']
                                            )
                                        }
                                    />
                                </td>
                                <td>
                                    <input
                                        type="button"
                                        className="btnMath"
                                        name="btnMath"
                                        value="*"
                                        onClick={() =>
                                            commonHandlers.handleCalcExpression([...calcExpression, '*'], [...checkCalcExpression, '*'])
                                        }
                                    />
                                </td>
                            </tr>
                            <tr>
                                <td>
                                    <input
                                        type="button"
                                        className="btnNum"
                                        name="btnNum"
                                        value="1"
                                        onClick={() =>
                                            commonHandlers.handleCalcExpression([...calcExpression, '1'], [...checkCalcExpression, '1'])
                                        }
                                    />
                                </td>
                                <td>
                                    <input
                                        type="button"
                                        className="btnNum"
                                        name="btnNum"
                                        value="2"
                                        onClick={() =>
                                            commonHandlers.handleCalcExpression([...calcExpression, '2'], [...checkCalcExpression, '2'])
                                        }
                                    />
                                </td>
                                <td>
                                    <input
                                        type="button"
                                        className="btnNum"
                                        name="btnNum"
                                        value="3"
                                        onClick={() =>
                                            commonHandlers.handleCalcExpression([...calcExpression, '3'], [...checkCalcExpression, '3'])
                                        }
                                    />
                                </td>
                                <td>
                                    <input
                                        type="button"
                                        className="btnOpps"
                                        name="btnOpps"
                                        value="&radic;"
                                        onClick={() =>
                                            checkNum(`Math.sqrt(${displayBoxFalsy})`) &&
                                            commonHandlers.handleCalcExpression(
                                                ['sqrt(', ...calcExpression, ')'],
                                                ['Math.sqrt(', ...checkCalcExpression, ')']
                                            )
                                        }
                                    />
                                </td>
                                <td>
                                    <input
                                        type="button"
                                        className="btnMath"
                                        name="btnMath"
                                        value="-"
                                        onClick={() =>
                                            commonHandlers.handleCalcExpression([...calcExpression, '-'], [...checkCalcExpression, '-'])
                                        }
                                    />
                                </td>
                            </tr>
                            <tr>
                                <td>
                                    <input
                                        type="button"
                                        className="btnMath"
                                        name="btnMath"
                                        value="+/-"
                                        onClick={() => {
                                            if (checkNum(displayBoxFalsy)) {
                                                if (window.eval(displayBoxFalsy) < 0 && checkCalcExpression[0] === '-') {
                                                    commonHandlers.handleCalcExpression(
                                                        calcExpression.slice(1),
                                                        checkCalcExpression.slice(1)
                                                    );
                                                } else if (window.eval(displayBoxFalsy) < 0 && checkCalcExpression[0] === '-(') {
                                                    commonHandlers.handleCalcExpression(
                                                        calcExpression.slice(1, -1),
                                                        checkCalcExpression.slice(1, -1)
                                                    );
                                                } else if (window.eval(displayBoxFalsy) > 0) {
                                                    commonHandlers.handleCalcExpression(
                                                        ['-(', ...calcExpression, ')'],
                                                        ['-(', ...checkCalcExpression, ')']
                                                    );
                                                }
                                            }
                                        }}
                                    />
                                </td>
                                <td>
                                    <input
                                        type="button"
                                        className="btnNum"
                                        name="btnNum"
                                        value="0"
                                        onClick={() =>
                                            commonHandlers.handleCalcExpression([...calcExpression, '0'], [...checkCalcExpression, '0'])
                                        }
                                    />
                                </td>
                                <td>
                                    <input
                                        type="button"
                                        className="btnMath"
                                        name="btnMath"
                                        value="&#46;"
                                        onClick={() =>
                                            commonHandlers.handleCalcExpression([...calcExpression, `.`], [...checkCalcExpression, '.'])
                                        }
                                    />
                                </td>
                                <td>
                                    <input
                                        type="button"
                                        className="btnOpps"
                                        name="btnOpps"
                                        value="x&#50;"
                                        onClick={() =>
                                            checkNum(`Math.pow(${displayBoxFalsy},2)`) &&
                                            commonHandlers.handleCalcExpression(
                                                ['pow(', ...calcExpression, ',2)'],
                                                ['Math.pow(', ...checkCalcExpression, ',2)']
                                            )
                                        }
                                    />
                                </td>
                                <td>
                                    <input
                                        type="button"
                                        className="btnMath"
                                        name="btnMath"
                                        value="+"
                                        onClick={() =>
                                            commonHandlers.handleCalcExpression([...calcExpression, `+`], [...checkCalcExpression, '+'])
                                        }
                                    />
                                </td>
                            </tr>
                            <tr>
                                <td>
                                    <input
                                        type="button"
                                        className="btnMath"
                                        name="btnMath"
                                        value="("
                                        onClick={() =>
                                            commonHandlers.handleCalcExpression([...calcExpression, `(`], [...checkCalcExpression, '('])
                                        }
                                    />
                                </td>
                                <td>
                                    <input
                                        type="button"
                                        className="btnMath"
                                        name="btnMath"
                                        value=")"
                                        onClick={() =>
                                            commonHandlers.handleCalcExpression([...calcExpression, `)`], [...checkCalcExpression, ')'])
                                        }
                                    />
                                </td>
                                <td>
                                    <input
                                        type="button"
                                        className="btnMath"
                                        name="btnMath"
                                        value="cos"
                                        onClick={() =>
                                            checkNum(`Math.cos(${displayBoxFalsy})`) &&
                                            commonHandlers.handleCalcExpression(
                                                ['cos(', ...calcExpression, ')'],
                                                ['Math.cos(', ...checkCalcExpression, ')']
                                            )
                                        }
                                    />
                                </td>
                                <td>
                                    <input
                                        type="button"
                                        className="btnMath"
                                        name="btnMath"
                                        value="sin"
                                        onClick={() =>
                                            checkNum(`Math.sin(${displayBoxFalsy})`) &&
                                            commonHandlers.handleCalcExpression(
                                                ['sin(', ...calcExpression, ')'],
                                                ['Math.sin(', ...checkCalcExpression, ')']
                                            )
                                        }
                                    />
                                </td>
                                <td>
                                    <input
                                        type="button"
                                        className="btnMath"
                                        name="btnMath"
                                        value="tan"
                                        onClick={() =>
                                            checkNum(`Math.tan(${displayBoxFalsy})`) &&
                                            commonHandlers.handleCalcExpression(
                                                ['tan(', ...calcExpression, ')'],
                                                ['Math.tan(', ...checkCalcExpression, ')']
                                            )
                                        }
                                    />
                                </td>
                            </tr>
                        </tbody>
                    </table>
                </form>
            </Box>
            {/* </Box> */}
        </Modal>
    );
}

export default ModalHandbookInterestRate;
