import * as React from 'react';
import Box from '@mui/material/Box';
import { DataGrid } from '@mui/x-data-grid';

export default function DataTable({ commonStates }) {
    return (
        <Box
            sx={{
                height: 642,
                width: '100%',
                '& .super-app-theme--cell': {
                    backgroundColor: '#fff',
                    display: 'flex',
                    justifyContent: 'left'
                },
                '& .super-app.negative': {
                    backgroundColor: '#d47483',
                    display: 'flex',
                    justifyContent: 'left'
                },
                '& .super-app.positive': {
                    backgroundColor: 'transparent',
                    display: 'flex',
                    justifyContent: 'left'
                }
            }}
        >
            <DataGrid rows={commonStates.rowsState} columns={commonStates.columnsState} pageSize={10} rowsPerPageOptions={[10]} />
        </Box>
    );
}
